.title_container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1em;
  }
  
  .title_container h1 {
    margin: 0;
    padding: 0.4em;
    background-color: #222;
    color: #ffbb33;
  }


.project_details{
    width: 100%;
    padding: 2em;

}
.project_details h2,
.project_details p{
    margin-bottom: 0.5em;
}

.project_details h1{
    background-color: #222;
    color: #ffbb33;
    padding: 0.4em;
}

.project_details span{
    font-weight: bold;
}

.details_container,
.service_form_container{
    width: 100%;
    border-bottom: 1px solid #7a7a7a;
    margin-bottom: 1.2em;
    padding-bottom: 1.2em;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}

.btn{
    background-color: #222;
    color: #fff;
    padding: 0.5em 1em;
    text-decoration: none;
    transition: 0.5s;
    cursor: pointer;
    max-height: 40px;
    border: none;
    border-radius: 5px;
}

.btn:hover{
    color: #ffbb33;
}

.project_info{
    width: 100%;

}